import React, { useCallback } from 'react';
import { Grid, Chip, makeStyles, Box, Theme } from '@material-ui/core';
import { Post } from 'models/post';
import Markdown from 'components/Utils/Markdown';
import * as Icon from 'react-feather';
import { Link } from 'gatsby';
import { usePageRouteUtils } from 'hooks/usePageRouteUtils';
import RelatedPosts from './RelatedPosts';
import PostHeader from './Header';
import Languages from './Languages';
import { Logger } from 'utils/loggers';

const useStyles = makeStyles<Theme>(({ spacing, typography }) => ({
  thumbnail: {
    paddingTop: '42.8%',
    display: 'block',
    position: 'relative',
    marginBottom: spacing(2),
    '& img': {
      position: 'absolute',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      width: '100%',
      height: '100%',
      objectFit: 'cover',
      margin: 0,
    },
  },
  languages: {
    margin: `${spacing(4)}px 0`,
  },
  relatedPosts: {
    marginTop: spacing(4),
  },
  tagWrapper: {
    margin: 0,
    padding: 0,
    marginTop: spacing(4),
  },
  tags: {
    listStyle: 'none',
    margin: 0,
    padding: 0,
    lineHeight: 1.1,
    '& a': {
      textDecoration: 'none',
      fontSize: typography.caption.fontSize,
    },
  },
}));

interface Props {
  post: Post;
  otherLangPosts: Array<Pick<Post, 'fields'>>;
  logger?: Logger;
}
const PostContents = ({ post, otherLangPosts, logger }: Props) => {
  const styles = useStyles();
  const { getTagPath } = usePageRouteUtils();
  const {
    frontmatter: { title, date, categories, tags, thumbnail },
    html,
  } = post;

  const handleOtherLanguageClick = useCallback(() => {
    logger?.click(' ');
  }, [logger]);

  const handleRelatedPostClick = useCallback(() => {
    logger?.click(' ');
  }, [logger]);

  const handleTagClick = useCallback(() => {
    logger?.click('');
  }, [logger]);

  return (
    <>
      {thumbnail && (
        <div className={styles.thumbnail}>
          <img src={thumbnail?.childImageSharp?.fluid?.src} alt={title} title={title} />
        </div>
      )}
      <PostHeader title={title ?? ''} date={date} categories={categories ?? []} />
      {otherLangPosts.length > 0 && (
        <Box className={styles.languages}>
          <Languages otherLangPosts={otherLangPosts} onClick={handleOtherLanguageClick} />
        </Box>
      )}
      <Markdown html={html ?? ''} />
      <section className={styles.relatedPosts}>
        <RelatedPosts tags={tags ?? []} currentPost={post} onClick={handleRelatedPostClick} />
      </section>
      {tags && (
        <Grid container className={styles.tagWrapper} component="ul" spacing={1}>
          <Grid item component="li" className={styles.tags}>
            <Icon.Tag />
          </Grid>
          {tags.map((tag, index) => (
            <Grid item component="li" key={index} className={styles.tags}>
              <Link to={getTagPath(tag)} onClick={handleTagClick}>
                <Chip label={tag} size="small" clickable={true} />
              </Link>
            </Grid>
          ))}
        </Grid>
      )}
    </>
  );
};

export default PostContents;
